.inputDate {
    width: 130px;
}
  
.input-group-btn button {
    margin-top: -3px;
}
  
input {
    &[type="radio"], &[type="checkbox"] {
      margin: 0px;
      margin-top: -3px;
    }
}
  
.selectWrapper {
    width: 100%;
    overflow: hidden;
    position: relative;
    border: 1px solid #bbb;
    border-radius: 2px;
    background: #FFFFFF url('data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2211%22%20height%3D%2211%22%20viewBox%3D%220%200%2011%2011%22%3E%3Cpath%20d%3D%22M4.33%208.5L0%201L8.66%201z%22%20fill%3D%22%2300AEA9%22%2F%3E%3C%2Fsvg%3E') right 13px center no-repeat;
    select {
      padding: 12px 40px 12px 20px;
      line-height: 18px;
      width: 100%;
      border: none;
      box-shadow: none;
      background: transparent;
      background-image: none;
      -webkit-appearance: none;
      outline: none;
      cursor: pointer;
      -moz-appearance: none;
      text-indent: 0.01px;
      text-overflow: ellipsis;
    }
}

#search-result {
  position: absolute;
  top: 70px;
  left: 0;
  bottom: 0;
  right: 0;
  iframe {
    width: 100%;
    height: 100%;
    border: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    z-index: 999999;
  }
  h2 {
    visibility: hidden;
  }
  .close {
    margin-right: 14px;
  }
  .body {
    height: calc(100vh - 70px);
  }
}